import React from 'react';
import { Layout, Wrapper, CardHeader } from '../components';
import Helmet from 'react-helmet';
import { CardContent } from '@material-ui/core';
import styled from 'styled-components';
import { graphql, Link } from 'gatsby';

const HTMLTitle = '关于站点';

interface Props {
  data: {
    github: any;
  };
}

interface States {
  articles: [];
  title: string;
}

const Article = styled.article`
  h3 {
    margin: 64px 0;
  }
`;

const Chunk = styled.section`
  margin-bottom: 48px;
  h4 {
    margin-bottom: 24px;
  }
  p {
    font-size: 14px;
    margin-bottom: 8px;
  }
`;

export default class AboutPage extends React.Component<Props, States> {
  constructor(props: any) {
    super(props);
    this.state = {
      articles: this.props.data.github.repository.issues.edges,
      title: HTMLTitle,
    };
  }
  public render() {
    const { title } = this.state;
    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <CardHeader>
            <h2>{title}</h2>
          </CardHeader>
          <CardContent>
            <Article>
              <h3>宣传墙</h3>
              <Chunk>
                <h4>
                  <Link to="/users/">🔗使用PINKX的剧组</Link>
                </h4>
              </Chunk>
              <h3 id="help">帮助</h3>
              <Chunk>
                <h4>使用教程</h4>
                {!!this.state.articles &&
                  this.state.articles.map((article: any) => {
                    return (
                      <p key={article.node.resourcePath}>
                        <a href={`https://github.com${article.node.resourcePath}`} target="_black">
                          🔗{article.node.title}
                        </a>
                      </p>
                    );
                  })}
              </Chunk>
              <Chunk>
                <h4 id="qa">常见问题</h4>
                <p>暂无</p>
              </Chunk>
              <Chunk>
                <h4>意见反馈</h4>
                <p>第一种：发表微博 @PINKX编帖助手 或 @游里，或者私信他们。</p>
                <p>
                  第二种：填写{' '}
                  <a href="https://wj.qq.com/s2/3191208/5ec6/" target="_black">
                    🔗意见反馈表单。
                  </a>
                </p>
                <p>第三种：加入PINKX功能主题交流QQ群：982996001。</p>
              </Chunk>
              <Chunk>
                <h4>
                  <a href="https://wj.qq.com/s2/3283324/911f" target="_black">
                    🔗主题提交
                  </a>
                </h4>
              </Chunk>
              <Chunk>
                <h4>
                  <Link to="/changelog/">🔗更新记录</Link>
                </h4>
              </Chunk>
            </Article>
          </CardContent>
        </Wrapper>
      </Layout>
    );
  }
}

export const GitHubViewerQuery = graphql`
  query {
    github {
      repository(owner: "uuPin1", name: "pinkx") {
        name
        issues(first: 10, labels: ["BLOG", "tutorial"], orderBy: { direction: ASC, field: COMMENTS }) {
          edges {
            node {
              title
              bodyHTML
              resourcePath
            }
          }
        }
      }
    }
  }
`;
